'use client';

import { useState, useEffect } from 'react';
import { useParams } from 'next/navigation';
import Link from 'next/link';

interface Blog {
  _id: string;
  title: string;
  slug: string;
  content: string;
  excerpt: string;
  image: string;
  altText: string;
  author: string;
  publishedDate: string;
  category: string;
  tags: string[];
  metaTitle: string;
  metaDescription: string;
}

export default function BlogDetailPage() {
  const params = useParams();
  const slug = params.slug as string;
  
  const [blog, setBlog] = useState<Blog | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [recentBlogs, setRecentBlogs] = useState<Blog[]>([]);

  useEffect(() => {
    if (slug) {
      fetchBlog();
      fetchRecentBlogs();
    }
  }, [slug]);

  const fetchBlog = async () => {
    try {
      const response = await fetch(`/api/blogs/${slug}`);
      const data = await response.json();
      
      if (data.success) {
        setBlog(data.data);
      } else {
        setError('Blog not found');
      }
    } catch (err) {
      console.error('Error fetching blog:', err);
      setError('Error loading blog');
    } finally {
      setLoading(false);
    }
  };

  const fetchRecentBlogs = async () => {
    try {
      const response = await fetch('/api/blogs');
      const data = await response.json();
      
      if (data.success) {
        // Get recent blogs excluding current one
        const recent = data.data.filter((b: Blog) => b.slug !== slug).slice(0, 5);
        setRecentBlogs(recent);
      }
    } catch (err) {
      console.error('Error fetching recent blogs:', err);
    }
  };

  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    const day = date.getDate();
    const month = date.toLocaleDateString('en-US', { month: 'long' });
    const year = date.getFullYear();
    
    // Add ordinal suffix to day
    const getOrdinalSuffix = (day: number) => {
      if (day >= 11 && day <= 13) {
        return 'th';
      }
      switch (day % 10) {
        case 1: return 'st';
        case 2: return 'nd';
        case 3: return 'rd';
        default: return 'th';
      }
    };
    
    return `${month} ${day}${getOrdinalSuffix(day)}, ${year}`;
  };

  if (loading) {
    return (
      <div className="d-flex align-items-center justify-content-center" style={{minHeight: '50vh'}}>
        <div className="text-center">
          <div className="spinner-border text-primary" role="status">
            <span className="visually-hidden">Loading...</span>
          </div>
          <p className="mt-3">Loading blog...</p>
        </div>
      </div>
    );
  }

  if (error || !blog) {
    return (
      <div className="d-flex align-items-center justify-content-center" style={{minHeight: '50vh'}}>
        <div className="text-center">
          <h2 className="text-muted">Blog Not Found</h2>
          <p className="text-muted">The blog you're looking for doesn't exist.</p>
          <Link href="/blogs" className="btn btn-primary">
            Back to Blogs
          </Link>
        </div>
      </div>
    );
  }

  return (
    <>
      <main>
        {/* Empty Box for spacing */}
        <div className="emptyBox"></div>

        {/* Breadcrumb */}
        <div className="breadcrumbWrapper">
        <div className="container-lg">
          <ol className="breadcrumb">
            <li className="breadcrumb-item"><a href="/">Home</a></li>
            <li className="breadcrumb-item">Media</li>
            <li className="breadcrumb-item"><a href="/blogs">Blogs</a></li>
            <li className="breadcrumb-item active">{blog.title}</li>
          </ol>
        </div>
        </div>

        {/* Blog Content Section */}
        <div className="w-100 padding position-relative bg-white news-section">
          <div className="position-absolute top-0 end-0 w-100 h-100 overflow-hidden" style={{ zIndex: -1 }}>
            <p className="hidden-title" data-position="left">News</p>
          </div>
          <div className="container-lg">
            <div className="row g-4 justify-content-center">
              <div className="col-lg-8 blogs-archive">
                <div className="inner pe-lg-5">
                  <div className="heading">
                    <h1 className="h2 fw-light text-primary">{blog.title}</h1>
                    <div className="d-flex align-items-center gap-3">
                      <span className="small d-flex align-items-center gap-1">
                        <i className="fa fa-calendar-day text-muted"></i>
                        {formatDate(blog.publishedDate)}
                      </span>
                      <span className="small d-flex align-items-center gap-1">
                        <i className="fa fa-user text-muted"></i>
                        {blog.author}
                      </span>
                      <span className="small d-flex align-items-center gap-1">
                        <i className="fa fa-tag text-muted"></i>
                        {blog.category.replace('-', ' ')}
                      </span>
                    </div>
                  </div>
                  <figure>
                    <img className="shadow-sm w-100" alt={blog.altText} src={blog.image} />
                  </figure>
                  <article className="blog-content-body">
                    <div dangerouslySetInnerHTML={{ __html: blog.content }} />
                  </article>
                  
                  {blog.tags && blog.tags.length > 0 && (
                    <div className="mt-4">
                      <h6 className="text-muted mb-2">Tags:</h6>
                      <div className="d-flex flex-wrap gap-2">
                        {blog.tags.map((tag, index) => (
                          <span key={index} className="badge bg-light text-dark">
                            {tag}
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                  
                  <div className="readmore mt-4">
                    <Link className="button button-sm" href="/blogs">Back</Link>
                  </div>
                </div>
              </div>
              <div className="col-lg-4 inside-blogs-aside">
                <div className="inner">
                  <div className="blog-asideBox">
                    <div className="heading mb-3"><span className="h5 mb-0 fw-light text-primary">Recent Posts</span></div>
                    <div className="side-recentBlogs">
                      {recentBlogs.map((recentBlog) => (
                        <div key={recentBlog._id} className="recentBlogs-box">
                          <Link href={`/blogs/${recentBlog.slug}`}>
                            <div className="img-fluid">
                              <img alt={recentBlog.title} className="h-100 object-cover" src={recentBlog.image} />
                            </div>
                            <span>{recentBlog.title}</span>
                          </Link>
                        </div>
                      ))}
                    </div>
                  </div>
                  <div className="blog-asideBox bg-light shadow-sm">
                    <div className="heading mb-3">
                      <span className="h5 mb-0 fw-light text-primary">Follow Us</span>
                    </div>
                    <div className="socialBtn d-flex justify-content-center justify-content-lg-start">
                      <a href="https://www.facebook.com/SuncityProjects/" target="_blank" rel="noreferrer" data-toggle="tooltip" data-placement="bottom" title="" data-bs-original-title="Facebook" aria-label="Facebook"><i className="fab fa-facebook-f"></i></a>
                      <a href="https://www.instagram.com/suncityprojectsofficial" target="_blank" rel="noreferrer" data-toggle="tooltip" data-placement="bottom" title="" data-bs-original-title="Instagram" aria-label="Instagram"><i className="fab fa-instagram"></i></a>
                      <a href="https://in.linkedin.com/company/suncityprojects" target="_blank" rel="noreferrer" data-toggle="tooltip" data-placement="bottom" title="" data-bs-original-title="LinkedIn" aria-label="LinkedIn"><i className="fab fa-linkedin-in"></i></a>
                      <a href="https://www.youtube.com/@SuncityProjectspvtltd" target="_blank" rel="noreferrer" data-toggle="tooltip" data-placement="bottom" title="" data-bs-original-title="Youtube" aria-label="Youtube"><i className="fab fa-youtube"></i></a>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>

      {/* AOS removed to prevent conflicts */}
    </>
  );
}
